import React from 'react';
import PropTypes from 'prop-types';
import { Wrapper } from 'components/pages/Login/Login.style';
import {
  StyledForm,
  LogoWrapper,
  StyledButton,
  StyledSpan,
} from 'components/pages/Login/Login.style';
import Input from 'components/atoms/Input/Input';
import { ReactComponent as Logo } from 'assets/icons/logo.svg';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { signup } from 'actions/authActions';
import { SIGNIN } from 'constants/routes';
import { HOME } from 'constants/routes';
import { Link } from 'react-router-dom';
import Loader from 'components/atoms/Loader/Loader';
import { useHistory } from 'react-router-dom';
import ErrorMessage from 'components/atoms/ErrorMessage/ErrorMessage';

const Register = (props) => {
  const history = useHistory();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const dispatch = useDispatch();
  const { apiCallProgress } = useSelector((state) => state.apiCallsReducer);
  const goHome = () => history.push(HOME);
  const onSubmit = (data) => {
    dispatch(signup(data.email, data.password, data.name, goHome));
  };
  return (
    <Wrapper>
      <LogoWrapper>
        <Logo />
      </LogoWrapper>
      <StyledForm onSubmit={handleSubmit(onSubmit)}>
        <Input
          placeholder="Email"
          type="email"
          {...register('email', {
            required: { value: true, message: 'Podaj adres email!' },
            pattern: {
              value: /\S+@\S+\.\S+/,
              message: 'Podana warto nie wyglda jak adres email!',
            },
          })}
        />
        {errors.email && (
          <ErrorMessage role="alert">{errors.email.message}</ErrorMessage>
        )}
        <Input
          placeholder="Twoje imi"
          type="name"
          {...register('name', {
            required: { value: true, message: 'Podaj swoje imi' },
          })}
        />
        {errors.name && (
          <ErrorMessage role="alert">{errors.name.message}</ErrorMessage>
        )}
        <Input
          placeholder="Haso"
          type="password"
          {...register('password', {
            required: { value: true, message: 'Podaj haso!' },
            minLength: {
              value: 6,
              message: 'haso musi posiada minimalnie 6 znakw!',
            },
          })}
        />
        {errors.password && (
          <ErrorMessage role="alert">{errors.password.message}</ErrorMessage>
        )}
        <StyledButton disabled={apiCallProgress === 1} type="submit">
          Zarejestruj si
        </StyledButton>
      </StyledForm>

      <StyledSpan>Masz ju konto?</StyledSpan>
      <Link to={SIGNIN}>
        <StyledSpan isColor={true}>Zaloguj si!</StyledSpan>
      </Link>

      {apiCallProgress === 1 ? <Loader /> : null}
    </Wrapper>
  );
};

Register.propTypes = {};

export default Register;
